import './index.less';

import React, { Component } from 'react';

import { Button } from 'antd';

class Errorpage extends Component{
  constructor(props){
      super()
      this.state = {
          second: 5,
          timmer: null
      }
  }
  componentDidMount(){ 
    let timmer = setInterval(()=>{
        if(this.state.second > 0){
            this.setState((state)=>{
                return {...state, second: state.second-1}
            })
        }else{
            const search = sessionStorage.getItem('search') ? sessionStorage.getItem('search') : ''
            window.open(' http://10.8.104.124:18061/#/login'+search, '_self')
            clearInterval(timmer)
            this.setState({timmer: null})
        }
    }, 1000)
    
    this.setState({timmer})
  }

  onClick = () => {
    const search = sessionStorage.getItem('search') ? sessionStorage.getItem('search') : ''
    window.open(' http://10.8.104.124:18061/#/login'+search, '_self')
  }

  componentWillUnmount(){
    if(this.state.timmer) clearInterval(this.state.timmer)
  }

  render(){
    return(
      <div className="jump-wrap">
        <div className='icon-generic'></div>
        <h1><span>访问失败</span></h1>
        <span className="tips">提示：系统将会在{this.state.second}s后自动返回一体化平台</span>
        <Button type='primary' onClick={this.onClick}>返回一体化平台</Button>
      </div>
    )
  }
}

export default Errorpage;