import React,{Component} from 'react';
import {Form,Input,Select} from 'antd';

class FormList extends Component{

    componentDidMount(){
        this.props.onRef(this);
        const {record} = this.props;
        const {setFieldsValue} = this.props.form;
        if(record){
            let params = {
                name: record.name,
                type: record.type ? 'true' : 'false',
                description: record.description
            }
            setFieldsValue(params)
        }
    }

    render(){
        const {getFieldDecorator, getFieldValue}=this.props.form;
        const {record} = this.props;
        const recordConfig = record ? JSON.parse(record.config) : {}
        const config={
            rules:[{
                required:true,
                message:'Please input!'
            }]
        }
        return(
            <Form labelCol={{span:6}} wrapperCol={{span:12}}>
                <Form.Item label='数据源名称'>
                    {
                        getFieldDecorator('name',config)(<Input placeholder='请输入数据源名称' />)
                    }
                </Form.Item>
                <Form.Item label='数据源类型'>
                    {
                        getFieldDecorator('type',config)(
                        <Select placeholder='请选择数据源类型'>
                            <Select.Option value='true'>MySQL</Select.Option>
                            <Select.Option value='false'>Hbase</Select.Option>
                        </Select>)
                    }
                </Form.Item>
                {getFieldValue('type')==='true' && <>
                    <Form.Item label='数据源url'>
                        {
                            getFieldDecorator('url',{
                                initialValue: record ? record.url : undefined,
                                ...config,  
                            })(<Input placeholder='请输入数据源url' />)
                        }
                    </Form.Item>
                    <Form.Item label='数据源账号'>
                        {
                            getFieldDecorator('username',{
                                initialValue: record ? record.userName : undefined,
                                ...config,  
                            })(<Input placeholder='请输入数据源账号' />)
                        }
                    </Form.Item>
                    <Form.Item label='数据源密码'>
                        {
                            getFieldDecorator('password',{
                                initialValue: record ? record.password : undefined,
                                ...config,  
                            })(<Input.Password placeholder='请输入数据源密码' />)
                        }
                    </Form.Item>
                    <Form.Item label='描述'>
                        {
                            getFieldDecorator('description',{
                                initialValue: record ? record.description : undefined, 
                            })(<Input.TextArea />)
                        }
                    </Form.Item>
                </>}
                {getFieldValue('type')==='false' && <>
                    <Form.Item label='hBaseMaster'>
                        {
                            getFieldDecorator('hBaseMaster',{
                                initialValue: record ? recordConfig['hbase.master'] : undefined,
                                ...config,  
                            })(<Input placeholder='请输入hBaseMaster' />)
                        }
                    </Form.Item>
                    <Form.Item label='zkQuorum'>
                        {
                            getFieldDecorator('zookeeperQuorum',{
                                initialValue: record ? recordConfig['hbase.zookeeper.quorum'] : undefined,
                                ...config,
                            })(<Input placeholder='请输入zookeeperQuorum' />)
                        }
                    </Form.Item>
                    <Form.Item label='zkClientport'>
                        {
                            getFieldDecorator('zookeepeClientport',{
                                initialValue: record ? recordConfig['hbase.zookeeper.property.clientport'] : undefined,
                                ...config,  
                            })(<Input placeholder='请输入zookeepeClientport' />)
                        }
                    </Form.Item>
                    <Form.Item label='描述'>
                        {
                            getFieldDecorator('description',{
                                initialValue: record ? record.description : undefined,
                            })(<Input.TextArea />)
                        }
                    </Form.Item>
                </>}
            </Form>
        )
    }
}

export default Form.create()(FormList);