

import React from 'react';

import {
  Redirect,
  Route,
  Switch,
} from 'react-router-dom';

import CustomComponent from './pages/customcomponent';
import Dataset from './pages/dataset';
import DataSource from './pages/datasource';
import ChartsConfig from './pages/editorCharts';
import Home from './pages/home';
import LuckySheet from './pages/luckysheet';
import Model from './pages/model';
import Process from './pages/process';
import CreateExperiment from './pages/process/createExperiment.js';
import CreateProcess from './pages/process/createProcess.js';
import RunExperiment from './pages/process/runExperiment.js';
import templateManage from './pages/template';
import CheckTemplate from './pages/template/CheckTemplate';
import UserMgt from './pages/user'
// import Login from './pages/login'

const PrivateRoute = ({component: Component, ...rest}) => (
    // <Route {...rest} render={(props) => (  // 判断是否登录
    //   !!isAuthenticated()
    //     ? <Component {...props} />
    //     : <Redirect to={{
    //       pathname: '/login',
    //       state: {from: props.location}
    //     }}/>
    // )}/>
    <Route {...rest} render={(props) => (  // 判断是否登录
         <Component {...props} />
    )}/>
)

const Routes =
    <Switch>
        {/* <Route exact path='/login' component={Login}></Route> */}
        <PrivateRoute exact path='/index/home' component={Home}/>
        <PrivateRoute exact path='/index/dataset' component={Dataset}/>
        <PrivateRoute exact path='/index/model' component={Model}/>
        <PrivateRoute exact path='/index/component' component={CustomComponent}/>

        <PrivateRoute exact path='/index/template' component={templateManage}/>
        <PrivateRoute exact path='/index/template/check' component={CheckTemplate}/>

        <PrivateRoute exact path='/index/process' component={Process}/>
        <PrivateRoute exact path='/index/process/create' component={CreateProcess}/>

        <PrivateRoute exact path='/index/experiment/create' component={CreateExperiment}/>
        <PrivateRoute exact path='/index/experiment/run' component={RunExperiment}/>

        <PrivateRoute exact path='/index/chartconfig' component={ChartsConfig}/>

        <PrivateRoute exact path='/index/luckysheet' component={LuckySheet}/>
        <PrivateRoute exact path='/index/user' component={UserMgt}/>
        <PrivateRoute exact path='/index/datasource' component={DataSource}/>
        
        <Redirect exact from='/index' to='/index/home'/>
    </Switch>

export { Routes };