import Axios from '../utils/request';

export async function addDataSource(params){
    return Axios({
        url:'/datasource/newUserDataSource',
        method:'POST',
        params
    })
}
export async function previewDataSource(){
    return Axios({
        url:'/datasource/previewDataSource',
        method:'GET'
    })
}
export async function deleteDataSource(params){
    return Axios({
        url:'/datasource/deleteDataSource',
        method:'POST',
        params
    })
}
export async function updateDataSource(params){
    return Axios({
        url:'/datasource/updateDataSource',
        method:'POST',
        params
    })
}
export async function addHBaseDatasource(params){
    return Axios({
        url:'/datasource/addHBaseDatasource',
        method:'POST',
        params
    })
}
export async function updateHBaseDatasource(params){
    return Axios({
        url:'/datasource/updateHBaseDatasource',
        method:'POST',
        params
    })
}