import Axios from '../utils/request';

export async function login(params) {
    return Axios({
      url: '/login',
      method: 'POST',
      params
    })
}

export async function logout(params) {
  return Axios({
    url: '/logout',
    method: 'POST',
    params
  })
}

export async function register(params) {
  return Axios({
    url: '/register',
    method: 'POST',
    params
  })
}

// 一体化平台跳转登录
export async function uumsLogin(params) {
  return Axios({
    url: '/uums/users/login',
    method: 'POST',
    params
  })
}


// 获取其他平台的跳转链接
export async function getJumpableApplication(params) {
  return Axios({
    url: '/uums/users/getJumpableApplication',
    method: 'POST',
    params
  })
}

