import React,{Component} from 'react';
import {Divider, Form,Input,Select} from 'antd';

class FormList extends Component{

    componentDidMount(){
        const {record} = this.props;
        const {setFieldsValue} = this.props.form;
        if(record){
            let params = {
                username: record.userName,
                password: record.password,
                is_adminstrator: record.type.toString(),
                email:record.email,
                mobile:record.mobile,
                userCode:record.userCode
            }
            setFieldsValue(params)
        }
    }

    render(){
        const {getFieldDecorator}=this.props.form;
        const config={
            rules:[{
                required:true,
                message:'Please input!'
            }]
        }
        return(
            <Form labelCol={{span:6}} wrapperCol={{span:12}}>
                { !this.props.record && 
                    <div>
                        <Form.Item label='用户名'>
                        {
                            getFieldDecorator('username',config)(<Input placeholder='请输入用户名' />)
                        }
                    </Form.Item>
                    <Form.Item label='密码'>
                        {
                            getFieldDecorator('password',config)(<Input.Password placeholder='请输入密码' />)
                        }
                    </Form.Item>
                    </div>
                }
                <Form.Item label='邮箱'>
                    {
                        getFieldDecorator('email',{rules:[{...config.rules[0],type:'email'}]})(<Input placeholder='请输入邮箱' />)
                    }
                </Form.Item>
                <Form.Item label='手机号'>
                    {
                        getFieldDecorator('mobile',config)(<Input placeholder='请输入手机号' />)
                    }
                </Form.Item>
                {
                    this.props.record && <Form.Item label='一体化名称'>
                    {
                        getFieldDecorator('userCode',config)(<Input placeholder='请输入一体化名称' />)
                    }
                </Form.Item>
                }
                {/* <Form.Item label='用户类型'>
                    {
                        getFieldDecorator('is_adminstrator',config)(
                        <Select placeholder='请选择用户类型'>
                            <Select.Option value='1'>管理员</Select.Option>
                            <Select.Option value='0'>普通用户</Select.Option>
                        </Select>)
                    }
                </Form.Item> */}
            </Form>
        )
    }
}

export default Form.create()(FormList);