import React from 'react'
import * as echarts from 'echarts'
import JSZip from 'jszip'
import FileSaver from 'file-saver'
import styles from './index.module.less'
import {actionCreator} from '../../store'

import {Menu} from 'antd'
import { useDispatch, useSelector } from 'react-redux';


export default function ContextMenu(props){
  const {activeIndex }=props;

  const dispatch =useDispatch();
  const charts = useSelector(state=>state.charts.chartData)
  
  const deleteChart=()=>{
    // console.log(activeIndex);
    dispatch(actionCreator.deleteChart(activeIndex));
  }
  const downloadPic = () => {
    const dom = document.getElementsByClassName('echarts-for-react')[activeIndex]
    console.log(dom)
    const echartDom = echarts.init(dom)
    const url = echartDom.getDataURL({type:'png',backgroundColor: '#fff'})
    var a = document.createElement('a');
    var fileName = `chart${activeIndex+1}.png`
    a.download = fileName;
    // a.href = res.data.data.content;
    a.href = url;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }
  const downloadAll = () => {
    var zip = new JSZip()
    var zimg = zip.folder('charts')
    const doms = document.getElementsByClassName('echarts-for-react')
    const domArr = Array.from(doms)
    const echartDoms = domArr.map(item=>echarts.init(item))
    const urls = echartDoms.map(item=>item.getDataURL({type:'png',backgroundColor: '#fff'}))
    for (var i = 0; i < urls.length; i++) {
      var src = urls[i]
      zimg.file(`chart${i}.png`, src.substring(22), {base64: true})
    }
    zip.generateAsync({type: 'blob'}).then(function (content) {
      // see FileSaver.js
      
      FileSaver.saveAs(content, 'charts.zip')
      
      })
  }

  return(
      <Menu className={styles.menu} id='menu'>
            <Menu.Item onClick={deleteChart}>删除</Menu.Item>
            <Menu.Item onClick={downloadPic}>下载图片</Menu.Item>
            <Menu.Item onClick={downloadAll}>全部下载</Menu.Item>
      </Menu>
  )
}