import Axios from '../utils/request';

export async function getUserList() {
    return Axios({
      url: '/admin/getAllUsers',
      method: 'GET',
    })
}

export async function diableUserPermission(params) {
    return Axios({
      url: '/diableUserPermission',
      method: 'POST',
      params
    })
}

export async function restoreUserPermission(params) {
    return Axios({
      url: '/restoreUserPermission',
      method: 'POST',
      params
    })
}

export async function addUser(params) {
    return Axios({
      url: '/register',
      method: 'POST',
      params
    })
}

export async function deleteUser(params) {
  return Axios({
    url: '/admin/deleteUser',
    method: 'POST',
    params
  })
}

export async function searchUser(params) {
  return Axios({
    url: '/searchUser',
    method: 'GET',
    params
  })
}

export async function editUserInfo(params) {
  return Axios({
    url: '/admin/editUser',
    method: 'POST',
    params
  })
}
export async function changeUserStatus(params) {
  return Axios({
    url: '/admin/changeUserStatus',
    method: 'POST',
    params
  })
}

