import './upload.less';

import React, { Component } from 'react';

import {
  Button,
  Checkbox,
  Col,
  Form,
  Icon,
  Input,
  message,
  Pagination,
  Radio,
  Row,
  Select,
  Tabs,
  Upload,
} from 'antd';

import {
  getAllFieldByTableName,
  getAllTableName,
  importApiDataSource,
  importDataSourceByField,
  importDataSourceBySql,
  importMySQLDataSource,
  insertUserDlDataset,
  uploadDataset,
  uploadUserDatasetFromMysql,
} from '../../api/dataset';
import { previewDataSource } from '../../api/datasource';

class UploadDataset extends Component {

  constructor(props){
    super(props)
    this.state = {
      fileList: [],
      k:0,
      registerType: 'file',  
      datasetType: 'machine',  // 'machine' | 'deap'  机器学习数据集 / 深度学习数据集
      nameValue:'',
      fileList: [],
      tableNameOptions: [],
      fieldOptions: [],
      datasourceOptions: [],
      checkAll: false,
      indeterminate: false,
      total:0,
      page: 1
    }
  }


  
  componentDidMount() {
    this.props.onRef(this)
  }

  submit_1= () => {
    const { getFieldValue, validateFields } = this.props.form
    validateFields((err,values) => {
      if(!err){
        let params = {
          datasetName: values.sourceName,
          tags: values.tags.split(','),
          datasetDesc: values.desc,
          // columnSeparator:values.colSpilt,
          // familyName:JSON.stringify(values.family),
          file:this.state.fileList[0],
          // userId:2
          }
          // const columnName=[];
          // for(let i=0; i<values.family.length; i++){
          //   const arr=values.columnName[i].split(',');
          //   for(let j=0; j<arr.length; j++){
          //     var str =values.family[i]+':'+arr[j];
          //     columnName.push(str);
          //   }
          // }
          // params.columnName=JSON.stringify(columnName);
          console.log(params);
          //异步请求
          uploadDataset(params).then(res => {
          // insertUserDataset(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
              this.props.datasetType === 'machine' ? this.props.getUploadData() : this.props.getDlUploadData();
            }else{
              message.error(newRes.message)
            }
          }).catch(err=>{
            console.log(err)
          })
          this.props.close();
      }
    });
  }

  submit_2 = () => {
    const { getFieldValue, validateFields } = this.props.form
    validateFields((err,values) => {
      if(!err){
        let params = {
          url: values.url,
          username: values.userName,
          password:values.password,
          tableName:values.tableName,
          datasetName:values.datasetName,
          tags:values.tags_mysql.split(','),
          datasetDesc:values.desc_mysql
          }
        
          console.log(params);
        
          //异步请求
          uploadUserDatasetFromMysql(params).then(res => {
          // insertUserDataset(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
              this.props.datasetType === 'machine' ? this.props.getUploadData() : this.props.getDlUploadData();
            }else{
              message.error(newRes.message)
            }
          }).catch(err=>{
            console.log(err)
          })
          this.props.close();
      }
    });
  }

  submit_3 = () => {
    const { getFieldValue, validateFields } = this.props.form
    validateFields((err,values) => {
      if(!err){
        let params = {
          datasetName: values.datasetName_1,
          tags: values.tags_1.split(','),
          datasetDesc: values.desc_1,
          isAnnotation:values.isAnnotation,
          originFileType:values.originFileType,
          file:this.state.fileList[0],
          // userId:2
          }
          
          console.log(params)
          //异步请求
          insertUserDlDataset(params).then(res => {
          // insertUserDataset(params).then((res)=>{
            const newRes=res.data.meta;
            if(newRes.code==='001'){
              message.success(newRes.message)
              this.props.datasetType === 'machine' ? this.props.getUploadData() : this.props.getDlUploadData();
            }else{
              message.error(newRes.message)
            }
          }).catch(err=>{
            console.log(err)
          })
          this.props.close();
      }
    });
  }


  clear = () => {
    this.setState({
      fileList: []
    })
    this.props.form.resetFields()
  }

  remove = k =>{
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    const newKeys=keys.filter(key => key !== k);
    form.setFieldsValue({
      keys:newKeys,
    });
  };

  add = () => {
    const { form } = this.props;
    const {k}=this.state;
    const keys = form.getFieldValue('keys');
    const nextKeys = keys.concat(k+1);
    form.setFieldsValue({
        keys:nextKeys
    });
    this.setState({
        k:k+1
    })
  };
  
  callback = (key) => {
    this.setState({datasetType: key})
  }

  getTableNames = (value) => {
    const item = this.state.datasourceOptions[value]
    if(item.type){
      const params = {
        databaseUrl: item.url,
        userName: item.userName,
        password: item.password,
        // databaseName: 'aiflow_studio'
      }
      getAllTableName(params).then((res)=>{
        if(res){
          if(res.data.meta.success){
            this.setState({
              tableNameOptions: res.data.data,
              isConnect: true
            })
            message.success("数据源连接成功！")
          }else{
            message.error('数据源连接失败！')
          }
        }
      }).catch(err=> message.error('数据源连接失败！'))
    }
  }

  onTableNameChange = (value) =>{
    const { getFieldValue, setFieldsValue} = this.props.form
    const item = this.state.datasourceOptions[getFieldValue('datasourceId')]

    const params = {
      databaseUrl: item.url,
      tableName: value,
      userName: item.userName,
      password: item.password,
    }
    getAllFieldByTableName(params).then((res)=>{
      if(res){
        if(res.data.meta.success){
          this.setState({
            fieldOptions: res.data.data,
            indeterminate: false,
            checkAll: false,
            page: 1,
            total: res.data.data.length
          })
          setFieldsValue({'field': []})
          // console.log(this.state.fieldOptions)
        }else{
          message.error('获取表字段失败')
        }
      }
    })
  }

  onCheckAllChange = e => {
    const { getFieldValue, setFieldsValue} = this.props.form 
    const {fieldOptions} = this.state;

    if(e.target.checked){
      setFieldsValue({'field': fieldOptions})
    }else{
      setFieldsValue({'field': []})
    }

    this.setState({
      indeterminate: false,
      checkAll: e.target.checked,
    });
  };

  onCheckBoxChange = checkedList =>{
    const {fieldOptions} = this.state;
    const {getFieldValue, setFieldsValue} = this.props.form
    console.log(getFieldValue('field'))
    this.setState({
      indeterminate: !!checkedList.length && checkedList.length < fieldOptions.length,
      checkAll: checkedList.length === fieldOptions.length,
    });
  }

  submit = () => {
    const { registerType } = this.state;  
    // uploadSelect === 'file' && this.handleFileUpload()
    registerType === 'mysql' && this.handleMysqlUpload()
    registerType === 'field' && this.handleFieldUpload()
    registerType === 'selfSql' && this.handleSelfSqlUpload()
    registerType === 'api' && this.handleApiUpload()
    // uploadSelect === 'train' && this.handleDeepUpload()
    // uploadSelect === 'predict' && this.handleSampleUpload()
  }

  handleMysqlUpload = () => {
    const { getFieldValue, resetFields } = this.props.form
    // const { buttonLoading, updateData } = this.props
    const params = {
      datasourceId: this.state.datasourceOptions[getFieldValue('datasourceId')].id,
      tableName: getFieldValue('tableName'),
      datasetName: getFieldValue('datasetName'),
      datasetDesc: getFieldValue('datasetDesc') ? getFieldValue('datasetDesc'): '',
      tags: getFieldValue('tags')
    }
    // buttonLoading(true)
    importMySQLDataSource(params).then(res=>{
      console.log(res);
      if(res){
        // buttonLoading(false)
        if(res.data.meta.code === '001'){
          // updateData()
          message.success(res.data.meta.message)
          resetFields()
        }else{
          message.error(res.data.meta.message)
        }
      }else{
        message.error("上传失败！")
      }
    })
  }

  handleFieldUpload = () => {
    const { getFieldValue} = this.props.form
    // const { buttonLoading, updateData } = this.props
    
    if(getFieldValue('tableName') && getFieldValue('datasetName') && getFieldValue('field')){
      const params = {
        datasourceId:this.state.datasourceOptions[getFieldValue('datasourceId')].id,
        tableName: getFieldValue('tableName'),
        datasetName: getFieldValue('datasetName'),
        datasetDesc: getFieldValue('datasetDesc') ? getFieldValue('datasetDesc'): '',
        field: getFieldValue('field'),
        tags: getFieldValue('tags'),
      }
      // buttonLoading(true)
      importDataSourceByField(params).then(res=>{
        if(res){
          // buttonLoading(false)
          if(res.data.meta.code === '001'){
            message.success(res.data.meta.message)
            // updateData()
            this.clear()
          }else{
            message.error(res.data.meta.message)
          }
        }else{
          message.error("上传失败！")
        }
      })
    }else{
      message.info("请输入完整信息！")
    }
  }

  handleSelfSqlUpload = () => {
    const { getFieldValue} = this.props.form
    // const { buttonLoading, updateData } = this.props
    if( getFieldValue('datasourceId') !== undefined && getFieldValue('datasetName') && getFieldValue('sql') ){
      const params = {
        datasourceId: this.state.datasourceOptions[getFieldValue('datasourceId')].id,
        datasetName: getFieldValue('datasetName'),
        datasetDesc: getFieldValue('datasetDesc') ? getFieldValue('datasetDesc'): '',
        sql: getFieldValue('sql'),
        tags: getFieldValue('tags'),
      }
      // buttonLoading(true)
      importDataSourceBySql(params).then(res=>{
        if(res){
          // buttonLoading(false)
          if(res.data.meta.code === '001'){
            message.success("上传成功！")
            // updateData()
            this.clear()
          }else{
            message.error("上传失败！")
          }
        }else{
          message.error(res.data.meta.message)
        }
      })
    }else{
      message.info("请输入完整信息！")
    }
  }

  handleApiUpload = () =>{
    const { getFieldValue} = this.props.form
    // const { buttonLoading, updateData } = this.props
    if(getFieldValue('sendUrl') && getFieldValue('datasetName')){
      const params = {
        sendUrl: getFieldValue('sendUrl'),
        datasetName: getFieldValue('datasetName'),
        datasetDesc: getFieldValue('datasetDesc') ? getFieldValue('datasetDesc'): '',
        datasetTags: getFieldValue('datasetTags')
      }
      // buttonLoading(true)
      importApiDataSource(params).then(res=>{
        if(res){
          // buttonLoading(false)
          if(res.data.meta.code === '001'){
            // updateData()
            message.success("上传成功！")
            // resetFields()
            this.clear()
          }else{
            message.error("上传失败！")
          }
        }else{
          message.error("上传失败！")
        }
      })
    }else{
      message.info("请输入完整信息！")
    }
  }

  onChange = (e) => {
    const {value} = e.target
    this.setState({ registerType: value })
    this.clear();
    this.setState({fieldOptions: []})
    if(value === 'mysql' || value === 'field' || value === 'selfSql'){
      previewDataSource().then(res=>{
        if(res.data.meta.code === '001'){
          console.log(res.data.data['dateSouce list'])
          this.setState({datasourceOptions: res.data.data['dateSouce list']})
        }else{
          message.error(res.data.meta.message)
        }
      })
    }
  }

  render(){
    const { fileList, registerType, datasetType, datasourceOptions} = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    getFieldDecorator('keys', { initialValue:[0]});
    const props = {
      onRemove: file => {
        this.setState(state => {
          const index = state.fileList.indexOf(file);
          const newFileList = state.fileList.slice();
          newFileList.splice(index, 1);
          return {
            fileList: newFileList,
          };
        });
      },
      beforeUpload: file => {
        this.setState(state => ({
          fileList: [...state.fileList, file],
        }));
        return false;
      },
      onChange:  info => {
        let fileList = [...info.fileList];
        console.log(fileList);
        fileList = fileList.slice(-1);
        console.log(fileList);
      },
      fileList,
    };
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
    const config = {
      rules: [{ required: true, message: 'Please input!' }],
    };
    const { TabPane } = Tabs;
    return(
      <>
      <Tabs onChange={this.callback} value={datasetType}>
        <TabPane tab="机器学习数据集" key="machine"></TabPane>
        <TabPane tab="深度学习数据集" key="deep"></TabPane>
      </Tabs>
      {
        datasetType === 'machine' && <Radio.Group onChange={this.onChange} value={registerType}>
          {/* <Radio value={0}>非表结构数据集</Radio> */}
          {/* <Radio value={1}>表结构数据集</Radio> */}
          {/* <Radio value={2}>深度学习数据集</Radio> */}
          <Radio value={'file'}>文件上传（支持csv、xls格式）</Radio>
          <Radio value={'mysql'}>数据库导入</Radio> <br/>
          <Radio value={'field'}>数据库表字段导入</Radio>
          <Radio value={'selfSql'}>自定义sql语句导入</Radio>
          <Radio value={'api'}>api导入</Radio>
        </Radio.Group>
      }
      { datasetType === 'machine' && <> 
        {
          registerType == 'file' &&
          <Form labelAlign='left'> 
          <Form.Item label='数据集名称'>
            {getFieldDecorator('sourceName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据集名称"/>)} 
          </Form.Item>

          <Form.Item label='数据集标签'
            name='tags'
          >
            {getFieldDecorator('tags', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
            (<Input.TextArea 
              placeholder='请输入标签，并以 , 分割'/>
              )}
          </Form.Item>

          <Form.Item label='数据集描述'>
            {getFieldDecorator('desc',{initialValue:''})(<Input.TextArea placeholder="请输入数据集描述" />)} 
          </Form.Item>

          <Form.Item required={true}>
            {getFieldDecorator('file',{rules: [{ required: true, message: 'Please input!' }]})(
            <Upload {...props}>
              <Button>
                <Icon type="upload" /> 选择文件
              </Button>
            </Upload>
            )} 
          </Form.Item>

          <Form.Item style={{textAlign:'center'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={this.clear}
            >
              重置
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={this.submit_1}
            >
              注册
            </Button>
          </Form.Item>
        </Form>
        }
        { registerType === 'mysql' &&
            <div style={{ marginTop: '19px' }}>
              <Form {...formItemLayout}>
              <Form.Item label="数据源">
                  {getFieldDecorator('datasourceId', config)(
                    <Select placeholder="请选择数据源" onChange={this.getTableNames}>
                      {
                        this.state.datasourceOptions.map((item, index)=>{
                          return (<Select.Option value={index}>{item.name}</Select.Option>)
                        })
                      }
                    </Select>
                  )}
              </Form.Item>
              {
                  datasourceOptions[getFieldValue('datasourceId')] && datasourceOptions[getFieldValue('datasourceId')].type &&
                  <Form.Item label="表名">
                    {getFieldDecorator('tableName', config)(
                      <Select placeholder="请选择表名" disabled={!this.state.isConnect}
                        onChange={this.onTableNameChange}  
                      >
                        {
                          this.state.tableNameOptions.map((item)=>{
                            return (<Select.Option value={item}>{item}</Select.Option>)
                          })
                        }
                      </Select>
                    )}
                  </Form.Item>
                }
                {
                  datasourceOptions[getFieldValue('datasourceId')] && !datasourceOptions[getFieldValue('datasourceId')].type &&
                  <Form.Item label="表名">
                    {getFieldDecorator('tableName', config)(<Input placeholder="请输入表名" />)}
                  </Form.Item>
                }
                <Form.Item label="数据集名称">
                  {getFieldDecorator('datasetName', config)(<Input placeholder="请输入数据集名称"/>)}
                </Form.Item>
                <Form.Item label="数据集标签">
                  {getFieldDecorator('tags', config)(<Input.TextArea placeholder="请输入数据集名称，以,分隔"/>)}
                </Form.Item>
                <Form.Item label="数据集描述">
                  {getFieldDecorator('datasetDesc')(<Input.TextArea placeholder="请输入数据集描述" style={{resize:'none'}}/>)}
                </Form.Item>
                <Form.Item style={{textAlign:'center'}}>
                  <Button style={{marginRight:'5px'}} type='primary' onClick={this.clear} >  重置 </Button>
                  <Button style={{marginLeft:'5px'}} type='primary' onClick={this.submit}> 注册 </Button>
                </Form.Item>
              </Form>
            </div>
        }
        { registerType === 'field' &&
            <div style={{ marginTop: '19px' }}>
              <Form {...formItemLayout}>
              <Form.Item label="数据源">
                  {getFieldDecorator('datasourceId', config)(
                    <Select placeholder="请选择数据源"  onChange={this.getTableNames}>
                      {
                        datasourceOptions.map((item, index)=>{
                          return (<Select.Option value={index}>{item.name}</Select.Option>)
                        })
                      }
                    </Select>
                  )}
              </Form.Item>
                {
                  datasourceOptions[getFieldValue('datasourceId')] && datasourceOptions[getFieldValue('datasourceId')].type &&
                  <Form.Item label="表名">
                    {getFieldDecorator('tableName', config)(
                      <Select placeholder="请选择表名" disabled={!this.state.isConnect}
                        onChange={this.onTableNameChange}  
                      >
                        {
                          this.state.tableNameOptions.map((item)=>{
                            return (<Select.Option value={item}>{item}</Select.Option>)
                          })
                        }
                      </Select>
                    )}
                  </Form.Item>
                }
                {
                  datasourceOptions[getFieldValue('datasourceId')] && !datasourceOptions[getFieldValue('datasourceId')].type &&
                  <Form.Item label="表名">
                    {getFieldDecorator('tableName', config)(<Input placeholder="请输入表名" />)}
                  </Form.Item>
                }
                {
                  datasourceOptions[getFieldValue('datasourceId')] && datasourceOptions[getFieldValue('datasourceId')].type && 
                  <Form.Item label="字段名">
                    <div className="site-checkbox-all-wrapper">
                      <Checkbox
                        indeterminate={this.state.indeterminate}
                        onChange={this.onCheckAllChange}
                        checked={this.state.checkAll}
                      >
                        全选
                      </Checkbox>
                    </div>
                   {getFieldDecorator('field',                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                )(
                     <Checkbox.Group key={getFieldValue('tableName')} onChange={this.onCheckBoxChange}>
                      <Row>
                         {
                           this.state.fieldOptions.map((item, index)=>{
                             return (
                               <Col className={(this.state.page-1)*6<=index&&index<Math.min(this.state.page*6, this.state.total)?'':'displayNone'}>
                                 <Checkbox value={item} key={item} style={{ lineHeight: '32px' }}>
                                  {item}
                                </Checkbox>
                               </Col>
                             )
                           })
                         }
                       </Row>
                     </Checkbox.Group>
                   )}
                   <div style={{borderTop:' 1px solid #e9e9e9'}}>
                      <Pagination 
                          size="small"
                          style={{float:'right', margin:'10px 0 0 0 '}}
                          current={this.state.page}
                          onChange={(page)=>{this.setState({page})}}
                          total={this.state.total} 
                          pageSize={6}
                          hideOnSinglePage={true}/>
                   </div>
                 </Form.Item>
                }
                {
                  datasourceOptions[getFieldValue('datasourceId')] && !datasourceOptions[getFieldValue('datasourceId')].type &&
                  <Form.Item label="字段名">
                    {getFieldDecorator('field', config)(<Input placeholder="请输入字段名，以逗号分隔" />)}
                  </Form.Item>
                }
                {/* <Form.Item label="表名">
                  {getFieldDecorator('tableName', config)(<Input placeholder="请输入表名" />)}
                </Form.Item>
                <Form.Item label="字段名">
                  {getFieldDecorator('field', config)(<Input placeholder="请输入字段名，以逗号分隔" />)}
                </Form.Item> */}
                <Form.Item label="数据集名称">
                  {getFieldDecorator('datasetName', config)(<Input placeholder="请输入数据集名称"/>)}
                </Form.Item>
                <Form.Item label="数据集标签">
                  {getFieldDecorator('tags', config)(<Input placeholder="数据集标签(,分隔)"/>)}
                </Form.Item>
                <Form.Item label="数据集描述">
                  {getFieldDecorator('datasetDesc')(<Input.TextArea placeholder="请输入数据集描述" style={{resize:'none'}}/>)}
                </Form.Item>
                <Form.Item style={{textAlign:'center'}}>
                  <Button style={{marginRight:'5px'}} type='primary' onClick={this.clear} >  重置 </Button>
                  <Button style={{marginLeft:'5px'}} type='primary' onClick={this.submit}> 注册 </Button>
                </Form.Item>
              </Form>
            </div>
        }
        { registerType === 'api' &&
            <div style={{ marginTop: '19px' }}>
              <p><span style={{color:'red'}}>*</span>&nbsp; 接口的数据必须是数组的形式，例如：[['a','b','c','d'],[1, 2, 3, 4],...]</p>
              <Form {...formItemLayout}>
                <Form.Item label="接口url">
                  {getFieldDecorator('sendUrl', config)(<Input placeholder="请输入接口url"/>)}
                </Form.Item>
                <Form.Item label="数据集名称">
                  {getFieldDecorator('datasetName', config)(<Input placeholder="请输入数据集名称"/>)}
                </Form.Item>
                <Form.Item label="数据集标签">
                  {getFieldDecorator('datasetTags')(<Input.TextArea placeholder="请输入数据集标签" style={{resize:'none'}}/>)}
                </Form.Item>
                <Form.Item label="数据集描述">
                  {getFieldDecorator('datasetDesc')(<Input.TextArea placeholder="请输入数据集描述" style={{resize:'none'}}/>)}
                </Form.Item>
                <Form.Item style={{textAlign:'center'}}>
                  <Button style={{marginRight:'5px'}} type='primary' onClick={this.clear} >  重置 </Button>
                  <Button style={{marginLeft:'5px'}} type='primary' onClick={this.submit}> 注册 </Button>
                </Form.Item>
              </Form>
            </div>
        }
        { registerType === 'selfSql' &&
              <div style={{ marginTop: '19px' }}>
              <Form {...formItemLayout}>
              <Form.Item label="数据源">
                  {getFieldDecorator('datasourceId', config)(
                    <Select placeholder="请选择数据源">
                      {
                        this.state.datasourceOptions.map((item, index)=>{
                          return (<Select.Option value={index}>{item.name}</Select.Option>)
                        })
                      }
                    </Select>
                  )}
                </Form.Item>
                <Form.Item label="sql查询语句">
                  {getFieldDecorator('sql', config)(<Input.TextArea placeholder="请输入sql查询语句" />)}
                </Form.Item>
                <Form.Item label="数据集名称">
                  {getFieldDecorator('datasetName', config)(<Input placeholder="请输入数据集名称"/>)}
                </Form.Item>
                <Form.Item label="数据集标签">
                  {getFieldDecorator('tags', config)(<Input placeholder="请输入数据集标签（,分隔）"/>)}
                </Form.Item>
                <Form.Item label="数据集描述">
                  {getFieldDecorator('datasetDesc')(<Input.TextArea placeholder="请输入数据集描述" style={{resize:'none'}}/>)}
                </Form.Item>
                <Form.Item style={{textAlign:'center'}}>
                  <Button style={{marginRight:'5px'}} type='primary' onClick={this.clear} >  重置 </Button>
                  <Button style={{marginLeft:'5px'}} type='primary' onClick={this.submit}> 注册 </Button>
                </Form.Item>
              </Form>
            </div>
            }
        {/* {
          registerType == 1 &&
          <Form labelAlign='left'> 
          <Form.Item label='数据库URL地址'>
            {getFieldDecorator('url',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据库URL地址"/>)} 
          </Form.Item>
          <Form.Item label='用户名'>
            {getFieldDecorator('userName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入用户名"/>)} 
          </Form.Item>
          <Form.Item label='密码'>
            {getFieldDecorator('password',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input.Password />)} 
          </Form.Item>
          <Form.Item label='表名'>
            {getFieldDecorator('tableName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入表名"/>)} 
          </Form.Item>
          <Form.Item label='数据集名称'>
            {getFieldDecorator('datasetName',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据集名称"/>)} 
          </Form.Item>


          <Form.Item label='数据集标签'
            name='tags'
          >
            {getFieldDecorator('tags_mysql', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
            (<Input.TextArea 
              placeholder='请输入标签，并以 , 分割'/>
              )}
          </Form.Item>

          <Form.Item label='数据集描述'>
            {getFieldDecorator('desc_mysql',{initialValue:''})(<Input.TextArea placeholder="请输入数据集描述" />)} 
          </Form.Item>

          <Form.Item style={{textAlign:'center'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={this.clear}
            >
              重置
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={this.submit_2}
            >
              注册
            </Button>
          </Form.Item>
        </Form>
        } */}
      </>}

      {
        datasetType == 'deep' &&
        <Form labelAlign='left'> 
          
          <Form.Item label='数据集名称'>
            {getFieldDecorator('datasetName_1',{rules: [{ required: true, message: 'Please input!' }]})
              (<Input placeholder="请输入数据集名称"/>)} 
          </Form.Item>

          <Form.Item label='数据集标签'
            name='tags'
          >
            {getFieldDecorator('tags_1', {initialValue:'',rules: [{ required: true, message: 'Please input!' }]})
            (<Input.TextArea 
              placeholder='请输入标签，并以 , 分割'/>
              )}
          </Form.Item>

          
          <Form.Item label="是否标注">
            {getFieldDecorator('isAnnotation')(
              <Radio.Group>
                <Radio value="1">是</Radio>
                <Radio value="0">否</Radio>
              </Radio.Group>,
            )}
          </Form.Item>

          <Form.Item label="数据集类型">
            {getFieldDecorator('originFileType')(
              <Radio.Group>
                <Radio value="0">文本</Radio>
                <Radio value="1">图片</Radio>
                <Radio value="2">其他</Radio>
              </Radio.Group>,
            )}
          </Form.Item>

          <Form.Item label='数据集描述'>
            {getFieldDecorator('desc_1',{initialValue:''})(<Input.TextArea placeholder="请输入数据集描述" />)} 
          </Form.Item>

          <Form.Item required={true}>
            {getFieldDecorator('file',{rules: [{ required: true, message: 'Please input!' }]})(
            <Upload {...props}>
              <Button>
                <Icon type="upload" /> 选择文件
              </Button>
            </Upload>
            )}
            
          </Form.Item>

          <Form.Item style={{textAlign:'center'}}>
            <Button style={{marginRight:'5px'}} type='primary'
              onClick={this.clear}
            >
              重置
            </Button>
            <Button style={{marginLeft:'5px'}} type='primary'
              onClick={this.submit_3}
            >
              注册
            </Button>
          </Form.Item>
        </Form>
      }
        
      </>
    )
  }
}

export default Form.create()(UploadDataset);