  // 自定义sql语句上传数据集
  export async function importDataSourceBySql(params) {
    const formData = new FormData();
    for(let key in params){
      formData.append(key, params[key]);
    }
    return Axios({
      url: '/dataset/importDataSourceBySql',
      method: 'POST',
      data: formData,
      processData: false,
      headers: {
        authorization: 'authorization-text',
      },
    })
  }
  
