import React, {useState, useImperativeHandle, forwardRef} from 'react'
import { Form, Select, Input, Button } from 'antd';
import {PIE_CHART, HEAT_MAP, LINE_CHART, AREA_CHART, BAR_CHART, DOT_CHART} from '../editorCharts/chartConstants'

const { Option } = Select;

const ChartForm = forwardRef(( props, ref ) => {
  useImperativeHandle(ref,()=>({
    values,
    chartType,
    setFormItemValue,
    type
  }))

  const [values, setValues] = useState({})
  const [type, setType] = useState({})
  const [chartType, setChartType] = useState('')
  const handleSubmit = e => {
    props.form.validateFields().then(values=>{
        console.log(values)
        setValues(values)
        props.form.resetFields()
        setChartType('')
        props.handleCancel()
    })
  };

  const handleSelectChange = value => {
    console.log(value);
    setChartType(value)
  };

  const setFormItemValue = (type,value) => {
    props.form.setFieldsValue({[type]:value})
  }
  const changeType = (name,v) => {
    setType({...type,[name]:v})
  }

    const { getFieldDecorator ,getFieldValue} = props.form;
    const selectAfter = (name)=>{
      return (
        <Select defaultValue="col" className="select-after" onChange={(v)=>changeType(name,v)}>
          <Option value="row">行</Option>
          <Option value="col">列</Option>
        </Select>
      );
    }
    return (
      <Form 
        labelCol={{ span: 8 }} wrapperCol={{ span: 15 }} onSubmit={handleSubmit}>
        <Form.Item label="图表类型" name='type' colon={false}>
        {getFieldDecorator('type', {
            rules: [{ required: true, message: '请选择图表类型' }],
          })(
                <Select onChange={handleSelectChange}>
                    <Option value={PIE_CHART}>饼图</Option>
                    <Option value={LINE_CHART}>折线图</Option>
                    <Option value={BAR_CHART}>柱状图</Option>
                    <Option value={DOT_CHART}>散点图</Option>
                    <Option value={HEAT_MAP}>热力图</Option>
                </Select>   
          )}
        </Form.Item>
          { 
            chartType === PIE_CHART &&
            <div>
                <Form.Item label='数据列' name=''>
                  {getFieldDecorator('value',{rules: [{ required: true, message: 'Please input!' }]})
                    (<Input placeholder='请输入数据对应第几列' addonAfter={selectAfter('value')}/>)} 
                </Form.Item>
            </div> 
          }
        
          { 
            chartType === LINE_CHART &&
            <div>
                <Form.Item label='x轴数据'>
                {getFieldDecorator('xData',{rules: [{ required: true, message: 'Please input!' }]})
                    (<Input placeholder='请输入x轴数据对应第几列'  addonAfter={selectAfter('xData')}/>)} 
                    
                </Form.Item>
                <Form.Item label='y轴数据'>
                {getFieldDecorator('yData',{rules: [{ required: true, message: 'Please input!' }]})
                    (<Input placeholder='请输入y轴数据对应第几列' addonAfter={selectAfter('yData')}/>)} 
                </Form.Item>
            </div>  
          }
          { 
            chartType === HEAT_MAP &&
            <div>
                <Form.Item label='x轴数据'>
                {getFieldDecorator('xData',{rules: [{ required: true, message: 'Please input!' }]})
                    (<Input placeholder='请输入x轴数据对应第几列' addonAfter={selectAfter('xData')}/>)} 
                    
                </Form.Item>
                <Form.Item label='y轴数据'>
                {getFieldDecorator('yData',{rules: [{ required: true, message: 'Please input!' }]})
                    (<Input placeholder='请输入y轴数据对应第几列' addonAfter={selectAfter('yData')}/>)} 
                </Form.Item>
                <Form.Item label='数值'>
                {getFieldDecorator('data',{rules: [{ required: true, message: 'Please input!' }]})
                    (<Input placeholder='请输入数值对应第几列' addonAfter={selectAfter('data')}/>)} 
                </Form.Item>
            </div>  
          }
       
        <Form.Item label=' ' colon={false}>
              <Button type='primary' htmlType='submit' style={{marginRight:10}}>确定</Button>
              <Button onClick={props.handleCancel}>取消</Button>
        </Form.Item>
      </Form>
    );
})

export default  Form.create()(ChartForm);
