import React, { Component } from 'react';

import {
  Button,
  Divider,
  message,
  Modal,
  PageHeader,
  Popconfirm,
  Spin,
  Table,
} from 'antd';

import {
  addDataSource,
  addHBaseDatasource,
  deleteDataSource,
  previewDataSource,
  updateDataSource,
  updateHBaseDatasource,
} from '../../api/datasource';
import FormList from './form';
import style from './index.module.less';

class DataSource extends Component{

    state={
        loading:false,
        dataSource:[],
        page:1,
        total:0,
        visible:false,
        updateVisible:false,
        updateRecord:{},
        buttonLoading:false,
    }

    getData=()=>{
        this.setState({
            loading:true
        })
        previewDataSource().then(res=>{
            var data=res.data;
            if(data.meta.code==='001'){
                var dataSource=data.data['dateSouce list'];
                console.log(dataSource)
                dataSource=dataSource.map((item,index)=>({
                    ...item,
                    index:index+1
                }))
                this.setState({
                    dataSource,
                    loading:false,
                    total:dataSource.length
                })
            }else{
                message.error('获取数据源失败!');
                this.setState({
                    loading:false
                })
            }
        })
        .catch(err=>{
            console.log(err);
        })
    }

    changePage=(page)=>{
        this.setState({
            page
        })                                       
    }

    addData=()=>{
        this.setState({
            visible:true
        })
    }

    clearForm=(f)=>{
        const {setFieldsValue}=f.props.form;
        setFieldsValue({
            name:'',
            type:'',
            url:'',
            username:'',
            password:'',
            description:''
        })
    }

    newDataSource=()=>{
        const {getFieldsValue}=this.add.props.form;
        var params=getFieldsValue();
        var f=true;
        this.setState({
            buttonLoading:true
        })
        console.log('datasource',params);
        if(params['description'] === undefined){
            params['description']=''
        }
        for(let i in params){
            if(params[i]===undefined){
                message.warning('请填写完整信息!');
                f=false;
                this.setState({
                    buttonLoading:false
                })
                break;
            }
        }
        var result
        if(f){
            if(params.type === 'true'){
                result = addDataSource(params)
            }else if(params.type === 'false'){
                result = addHBaseDatasource(params)
            }
            result.then(res=>{
                console.log(res.data);
                if(res.data.meta.code==='001'){
                    this.setState({
                        buttonLoading:false
                    })
                    message.success('添加数据源成功!');
                    this.clearForm(this.add);
                    this.getData();
                }else{
                    message.error('添加数据源失败!')
                    this.setState({
                        buttonLoading:false
                    })
                }
            })
            .catch(err=>{
                console.log(err);  
            })
        } 
    }

    updateDatasource=(record)=>{
        this.setState({
            updateVisible:true,
            updateRecord:record
        })
    }

    updateData=()=>{
        const {getFieldsValue}=this.update.props.form;
        var params=getFieldsValue();
        params={id:this.state.updateRecord.id, ...params};
        var f=true;
        this.setState({
            buttonLoading:true
        })
        console.log(params);
        for(let i in params){
            if(params[i]===undefined){
                message.warning('请填写完整信息!');
                f=false;
                this.setState({
                    buttonLoading:false
                })
                break;
            }
        }
        var result
        if(f){
            if(params.type === 'true'){
                result = updateDataSource(params)
            }else if(params.type === 'false'){
                result = updateHBaseDatasource(params)
            }
            result.then(res=>{
                console.log(res.data);
                if(res.data.meta.code==='001'){
                    this.setState({
                        buttonLoading:false,
                        updateVisible:false,
                    })
                    message.success('修改数据源成功!')
                    this.getData();
                }else{
                    message.error('修改数据源失败!');
                    this.setState({
                        buttonLoading:false
                    })
                }
            }).catch(err=>{
                console.log(err);
            })
        }   
    }   

    handleCancel=()=>{
        if(this.state.visible){
            this.clearForm(this.add);
        }
        this.setState({
            visible:false,
            updateVisible:false,
        })
    }
    deleteData=(record)=>{
        var params={datasourceId:record.id};
        deleteDataSource(params).then(res=>{
            if(res.data.meta.code==='001'){
                message.success('删除成功!');
                this.getData();
            }else{
                message.error('删除失败!');
            }
        })
        .catch(err=>{
            console.log(err);
        })
    }

    componentDidMount(){
        this.getData();
    }
    render(){
        const {loading,dataSource,total,page,visible,updateVisible,buttonLoading}=this.state;
        const columns=[
            {
                title: '序号',
                dataIndex: 'index',
                key: 'index',
            },
            {
                title:'数据源名称',
                dataIndex:'name',
                key:'name'
            },
            {
                title:'创建时间',
                dataIndex:'createTime',
                key:'createTime'
            },
            {
                title:'数据源账号',
                dataIndex:'userName',
                key:'userName'
            },
            {
                title:'数据源类型',
                dataIndex:'type',
                key:'type',
                render:(text,record)=>{
                    return text?'MySQL':'Hbase'
                }
            },
            {
                title:'操作',
                dataIndex:'action',
                key:'action',
                render:(text,record)=>{
                    return(
                        <span>
                            {/* <a>查看</a>
                            <Divider type='vertical'/> */}
                            <a onClick={()=>{this.updateDatasource(record)}}>修改</a>
                            <Divider type='vertical'/>
                            {/* <a onClick={()=>{this.deleteData(record)}}>删除</a> */}
                            <Popconfirm
                                title="删除后该数据将不再保留，确认删除吗？"
                                onConfirm={()=>{
                                this.deleteData(record)
                                }}
                                okText="确定"
                                cancelText="取消"
                            >
                                <a key="list-loadmore-edit">删除</a>
                            </Popconfirm>
                        </span>
                    )
                }
            }
        ]
        return(
            <div className={style.contentWrap}>
                <PageHeader
                    style={{border: '1px solid rgb(235, 237, 240)',marginBottom: '13px'}}
                    title='数据源管理'
                    subTitle='已添加数据库列表'
                    extra={
                        [
                            <Button type='primary' onClick={this.addData}>添加数据源</Button>
                        ]
                    }
                />
                <Spin spinning={loading}>
                    <Table
                        columns={columns}
                        dataSource={dataSource}
                        pagination={{
                            onChange:this.changePage,
                            pageSize:10,
                            total,
                            current:page
                        }}
                    />
                </Spin>
                {
                    visible && <Modal
                        visible={visible}
                        title='添加数据源'
                        okText='确定'
                        cancelText='取消'
                        onOk={this.newDataSource}
                        onCancel={this.handleCancel}
                        okButtonProps={{loading:buttonLoading}}
                    >
                        <div>
                            <FormList key={'newDatasource'} onRef={(ref)=>{this.add=ref}}></FormList>
                        </div>
                    </Modal>
                }{
                    updateVisible && <Modal
                        visible={updateVisible}
                        title='修改数据源'
                        okText='确定'
                        cancelText='取消'
                        onOk={this.updateData}
                        onCancel={this.handleCancel}
                    >
                        <div>
                            <FormList key={this.state.updateRecord.id} onRef={(ref)=>{this.update=ref}} record={this.state.updateRecord}></FormList>
                        </div>
                    </Modal>
                }
                        
        </div>
        )
    }
}
export default DataSource;