// import Logo from '../../common/loginLogo.jpg';
import './index.less';

import React from 'react';

import {
  Button,
  Form,
  Icon,
  Input,
  message,
} from 'antd';
import { withRouter } from 'react-router-dom';

import { login } from '../../api/login';

function Login(props){

  const { getFieldDecorator } = props.form;

  const handleSubmit = e => {
      e.preventDefault();
      props.form.validateFields((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values);
          const { username, password } = values;
          const params = { username, password };
          login(params).then((res)=>{
            if(res){
              res.data.meta.code === '001' ?  loginSet(res.data.data['user type'], res.data.data.uid) : message.error(res.data.meta.message);
            }
          });
        }
      });
    }

    const loginSet = (type, userId) => {
      sessionStorage.setItem("isLogin", true);
      sessionStorage.setItem("userType", type);
      sessionStorage.setItem("userId", userId);
      props.history.push('/index');
    }



  return (
    <div className='loginContainer'>
      {/* <div className='title'>
        <p className='topTitle'>AIFlow</p>
        <p className='mainTitle'>人工智能应用开发平台</p>
      </div> */}
       <div className='title'>
        <p className='topTitle'>WeShare-AI</p>
        <p className='subTitle'>WeShare Big Data Platform Brings New Productivity to Your Data</p>
        <p className='mainTitle'>AI智能分析平台</p>
      </div>
      <div className='loginForm'>
        <Form  className="login-form" onSubmit={handleSubmit} autoComplete="off">
          <Form.Item>
            {getFieldDecorator('username', {
              rules: [{ required: true, message: 'Please input your username!' }],
            })(
              <Input
                size="large"
                prefix={<Icon type="user" className="loginIcon" />}
                placeholder="用户名"
              />,
            )}
          </Form.Item>
          <Form.Item style={{marginBottom: '0px'}}>
            {getFieldDecorator('password', {
              rules: [{ required: true, message: 'Please input your Password!' }],
            })(
              <Input
                size="large"
                prefix={<Icon type="lock" className="loginIcon" />}
                type="password"
                placeholder="密码"
              />,
            )}
          </Form.Item>
          <Form.Item>
            {/* {getFieldDecorator('remember', {
              valuePropName: 'checked',
              initialValue: true,
            })(<Checkbox>记住密码</Checkbox>)} */}
            {/* <Link to='/register'>
              <a className="login-form-forgot" >
                注册新用户
              </a>
            </Link> */}
            <Button type="primary" size="large" htmlType="submit" className="login-form-button">
              登录
            </Button>
          </Form.Item>
        </Form>
      </div>
      <div className='foot'>
        <p>WeShare物联网大数据产品系列让您的数据焕发全新生产力   版权所有@河钢数字技术股份有限公司 | 版本4.1.0</p>
      </div>
    </div>
  )

}


export default Form.create()(withRouter(Login));