import './index.less';

import React, { Component } from 'react';

import {
  Icon,
  Result,
  Spin,
} from 'antd';
import { withRouter } from 'react-router-dom';

import { uumsLogin } from '../../api/login';

@withRouter
class middlepage extends Component{
  componentDidMount(){
    const {search} = this.props.location
    if(search){
      sessionStorage.setItem('search', search)
      const account = decodeURIComponent(search.split('account=')[1].split('&')[0]).replaceAll(" ","+");
      const params = { account };
      uumsLogin(params).then((res)=>{
        if(res){
          if(res.data && res.data.meta && res.data.meta.code === '001'){
            this.loginSet(res.data.data['user type'], res.data.data.uid, res.data.data.username)
          }else{
            this.props.history.push('/jumperror')
          }
        }
      }).catch(()=>{
        this.props.history.push('/jumperror')
      });
    }else{
      this.props.history.push('/jumperror')
    }
  }

  loginSet = (type, userId, username) => {
    sessionStorage.setItem("isLogin", true);
    sessionStorage.setItem("userType", type);
    sessionStorage.setItem("userId", userId);
    username && sessionStorage.setItem('username', username)
    this.props.history.push('/index');
  }

  render(){
    return(
      <>
        <Result
          style={{ 
            'maxWidth': '1100px',
            'margin': '76px auto'
          }}
          icon={<Icon type="smile" />}
          title="AI创作室"
          subTitle={<Spin spinning={true} tip="正在跳转中..."></Spin>}
        />,
      </>
    )
  }
}

export default middlepage;