import React from 'react';

import {
  Avatar,
  Dropdown,
  Icon,
  Menu,
  message,
} from 'antd';
import { withRouter } from 'react-router-dom';

// import logo from '../../common/logo.png';
import logo from '../../../public/homeLogo.png';
// import { MenuUnfoldOutlined, MenuFoldOutlined,} from '@ant-design/icons';
import {
  getJumpableApplication,
  logout,
} from '../../api/login';
import { menus } from '../../menusConfig';
import CustomMenu from './CustomMenu';
import Style from './index.module.less';

@withRouter 
class HeaderBar extends React.Component {
  constructor(){
    super();
    this.state = {
      username: '',
      titleMenu: null,
      plats: [],
    }
  }

  componentDidMount(){
    this.setState({username: localStorage.getItem('username')})
    // this.getMeun()
    if(sessionStorage.getItem('search')){
      this.getPlatFormData()
    }
  }

  logout = () => {
    logout().then(res=>{
      if(res){
        if(res.data.meta.code === "001"){
          this.props.history.push('/login')
          message.success(res.data.meta.message)
        }else{
          message.error(res.data.meta.message)
        }
      }
    })
  }

  getPlatFormData = () => {   // 获取其他平台的名字和链接
    getJumpableApplication().then(res=>{
      if(res){
        if(res.data.meta.code === '001'){
          let that = this
          this.setState({plats: res.data.data}, ()=>{
            that.getMeun()
          })
        }
      }
    })
  }

  getMeun = () => {
    const {plats} = this.state
    const search = sessionStorage.getItem('search') || ''

    const getUrl = (plat) => {
      if(plat.codingId === 'ABI'){
        const userType = sessionStorage.getItem('userType')
        const token = '1123598821738675201'
        if(userType === '1'){
          return plat.loginUrl + '?token='+token+'ADMIN'
        }else{
          return plat.loginUrl + '?token='+token+'USER'
        }
      }else{
        return plat.loginUrl + search
      }
    }
    const titleMenu = 
      <Menu className={Style.titleMenu}>
        {
          plats.map(plat=>{
            return plat.appName !== 'AI创作室' ? <Menu.Item className="menu-item" key={plat.id}>
              <a href={getUrl(plat)} target="_blank" rel="noopener noreferrer">
                {plat.appName}
              </a>
            </Menu.Item> : null
          })
        }
        <Menu.Item className={Style.menuItem}>
          <a href={' http://10.8.104.124:18061/#/login'+search} target="_blank" rel="noopener noreferrer">
            控制台
          </a>
        </Menu.Item>
      </Menu>
    this.setState({titleMenu})
  }

  render () {
    const menu = (
      <Menu className='menu'>
        <Menu.Item><span onClick={this.logout}>退出登录</span></Menu.Item>
      </Menu>
    )
    console.log(this.state)
    return (
      <div className={Style.HeaderBar}>
        <div style={{display: 'flex', alignItems: 'center'}}>
          <div style={styles.logo}>
            {
              sessionStorage.getItem('search') ?
                <>
                <img src={logo} style={{width:"36px", height: "36px", margin: '0 20px'}}></img> 工业大数据平台
                <div style={{height: 6, width: 6, borderRadius: 3, backgroundColor: '#fff', margin: '0 10px'}}></div>
                <Dropdown overlay={this.state.titleMenu}>
                  <div><span style={{fontSize: 16}}>AI创作室</span><Icon type="down" style={{fontSize:12}}></Icon></div>
                </Dropdown>
                </>
              :
                <><img src={logo} style={{width:"36px", height: "36px", margin: '0 20px'}}></img> AI智能分析平台</>
            }
            
          </div>
          <CustomMenu menus={menus}/>
        </div>
        <Dropdown overlay={menu} placement="bottomCenter" className={Style.avatar}>
          <Avatar icon="user" size='large' />
        </Dropdown>
      </div>
    )
  }
}

const styles = {
  logo: {
    width: 380,
    display: 'flex',
    alignItems: 'center',
    height: 50,
    overflow: 'hidden',
    color: '#fff',
    fontSize: '20px',
    fontFamily: 'Avenir,-apple-system,BlinkMacSystemFont,segoe ui,Roboto,helvetica neue,Arial,noto sans,sans-serif,apple color emoji,segoe ui emoji,segoe ui symbol,noto color emoji,sans-serif',
    lineHeight: '48px',
    // background: '#0C5190'
  }
}


export default HeaderBar