import React,{useEffect, useState} from 'react'
import {PageHeader, Table, Avatar, Icon, Popconfirm, Divider, message,Input,Button,Modal} from 'antd'
import { getUserList, deleteUser, searchUser, changeUserStatus, restoreUserPermission, addUser, editUserInfo} from '../../api/user'
import FormList from './formlist'
import style from './index.module.less'

function User(props){

    const [loading, setLoading] = useState(true)
    const [dataSource, setDataSource] = useState([])
    const [editRecord, setEditRecord] = useState({})
    const [visible, setVisible] = useState('0')
    const [page, setPage] = useState(1)
    const [total, setTotal] = useState(1)
    const [search, setSearch] = useState('')
    const [buttonLoading, setButtonLoading] = useState(false)
    const add = React.createRef(null)
    const edit = React.createRef(null)
    useEffect(()=>{
        if(!loading) return
        search === '' ? getUserData() : getSearchData(search)
    },[loading])

    const columns = [
        { align: 'center', render: ()=><Avatar size={40} icon={<Icon type="idcard" />} style={{ backgroundColor: '#27afff' }} />},
        {title: '用户名', dataIndex: 'userName',  key: 'userName', },
        {title: '状态', dataIndex: 'status', key: 'status', render: (text)=>{ return text == '1' ? '正常':'已禁用' } },
        { title: '用户类型', dataIndex: 'type', key: 'type', render: (text)=>{ return text == '1' ? '管理员':'普通用户' } },
        {title: '创建时间',dataIndex: 'createTime',key: 'createTime'},
        {title: '操作',dataIndex: 'action',key: 'action',
            render: (text, record)=>{
              // return <a href='javascript:void(0);' onClick={e=>this.delectComponent(record)}>删除</a>
              return (<span>
                <a onClick={()=>handleEdit(record)}>编辑</a>
                {
                  sessionStorage.getItem("userId") != record.id && <>
                  <Divider type='vertical'/>
                  <Popconfirm
                    title="删除后该数据将不再保留，确认删除吗？"
                    onConfirm={()=>{
                      handleDelete(record)
                    }}
                    okText="确定"
                    cancelText="取消"
                  >
                    <a>删除</a>
                  </Popconfirm>
                  <Divider type='vertical'/>
                  <Popconfirm
                    title="是否确定修改该用户的状态？"
                    onConfirm={()=>{
                      handleStatus(record)
                    }}
                    okText="确定"
                    cancelText="取消"
                  >
                    <a>{record.status == '1' ? '禁用':'恢复'}</a>
                  </Popconfirm></>
                }
                </span>)
            }
          },
    ]

    const getUserData = () => {
        const params ={
            pageNum: page,
            pageSize: 10
        }
        getUserList(params).then(res=>{
            console.log(res)
            if(res){
              setLoading(false)
              if(res.data.meta.code === '001'){
                // const total = res.data.data.Total
                const datasource = res.data.data.map((item,index)=>{
                return{
                    ...item, 
                    // index:index+1+(page-1)*10
                    index:index
                }
                })
                setDataSource(datasource)
                setTotal(res.data.data.length)
            }else{
                message.error(res.data.meta.message)
            }
            }
        }).catch(err=>setLoading(false))
    }
    const handleEdit = (record) =>{
        setEditRecord(record)
        setVisible('1')
    }
    const handleDelete = record => {
        const { id } = record
        deleteUser({id}).then(res=>{
            console.log(res)
            if(res){
              if(res.data.meta.code==='001'){
                message.success('删除成功！')
                setLoading(true)
              }else{
                message.error(res.data.meta.message  )
              }
            }
          })
    }
    const handleAddUser = () => {
        setVisible('2')
    }
    const handleSearch = value => {
        setPage(1)
        setLoading(true)
    }
    const onSearchChange = e => {
        setSearch(e.target.value)
        if(e.target.value === ''){
            setLoading(true)
        }
    }
    const getSearchData = value => {
        const params ={
          pageNum: page,
          pageSize: 10,
          userName: value
        }
        searchUser(params).then(res=>{
          console.log(res)
          if(res){
            setLoading(false)
            if(res.data.meta.code === '001'){
              const total = res.data.data.Total
              const datasource = res.data.data['User List'].map((item,index)=>{
                return{
                  ...item, index:index+1+(page-1)*10
                }
              })
              setDataSource(datasource)
              setTotal(total)
            }
          }
        }).catch(err=>setLoading(false))
    }
    const handleStatus = record => {
        const {id, status} = record;
        if(status == '1'){
          changeUserStatus({id,status:0}).then(res=>{
            if(res){
              if(res.data.meta.code === '001'){
                message.success('禁用用户成功！')
                setLoading(true)
              }
            }else{
              message.error(res.data.meta.message)
            }
          })
        }else{
          changeUserStatus({id,status:1}).then(res=>{
            if(res){
              if(res.data.meta.code === '001'){
                message.success('恢复用户成功！')
                setLoading(true)
              }
            }else{
              message.error(res.data.meta.message)
            }
          })
        }
      }
    // const changePage = () => {
    //     setLoading(true)
    // }
    const addNewUser = () => {
        // console.log(add)
        const {getFieldsValue}=add.current;
        const params = getFieldsValue();
        var f=true;
        setButtonLoading(true)
        for(let i in params){
          if(params[i]===undefined){
            message.warning('请填写完整信息!');
            f=false;
            setButtonLoading(false)
            break;
          }
        }
        if(f){
          addUser(params).then(res=>{
            if(res){
              if(res.data.meta.code === '001'){
                message.success('添加用户成功！')
                setPage(1)
                setButtonLoading(false)
                // add.current.resetFields()
                setVisible('0')
                setLoading(true)
              }else{
                message.error(res.data.meta.message)
                setButtonLoading(false)
              }
            }
          })
        }
    }
    const handleCancel = () => {
        setVisible('0')
    }
    const editUser = () => {
        const {getFieldsValue}=edit.current;
        const params = getFieldsValue();
        var f=true;
        setButtonLoading(true)
        for(let i in params){
          if(params[i]===undefined){
            message.warning('请填写完整信息!');
            f=false;
            setButtonLoading(false)
            break;
          }
        }
        if(f){
          editUserInfo({...params, id: editRecord.id}).then(res=>{
            if(res){
              if(res.data.meta.code === '001'){
                message.success('修改用户信息成功！')
                setButtonLoading(false)
                setVisible('0')
                setLoading(true)
              }else{
                message.error(res.data.meta.message)
              }
            }
          })
        }
    }
    
    

    return(
        <div className={style.contentWrap}>
            <PageHeader
                style={{border: '1px solid rgb(235, 237, 240)',marginBottom: '13px'}}
                title="用户管理" subTitle="已有用户列表"
                extra={[
                    <Button  onClick={handleAddUser} style={{display:'inline-block'}} type="primary">添加用户</Button>,
                    <Input.Search 
                      onSearch={value=>handleSearch(value)}
                      style={{width:'249px', float:"right"}}  
                      value={search}
                      onChange={onSearchChange}
                      placeholder="搜索用户"
                    />
                  ]}
            />
            <Table
                loading={{spinning:loading, tip:'loading...'}}
                dataSource={dataSource}
                columns={columns}
                pagination={{
                  onChange: p => {
                    // console.log(page);
                    setPage(p)
                    // changePage()
                  },
                  current:page,
                  total,
                  pageSize: 10,
                }}
            />
            <Modal visible={visible === '2'} title="添加用户" 
                    okText='确定'
                    cancelText='取消'
                    onOk={addNewUser}
                    onCancel={handleCancel}
                    okButtonProps={{loading:buttonLoading}}
                    destroyOnClose
                    >
                <FormList ref={add} key="addUser"></FormList>
            </Modal>
            <Modal visible={visible === '1'} title="编辑用户信息" 
                    okText='确定'
                    cancelText='取消'
                    onOk={editUser}
                    onCancel={handleCancel}
                    okButtonProps={{loading:buttonLoading}}
                    destroyOnClose
                    >
                <FormList ref={edit} key={editRecord.id} record={editRecord}></FormList>
            </Modal>
        </div>
    )
}
export default User